
@import '../common/stylus/style.css';
img.bg {
  width: 100%;
  height: 36%;
  -webkit-filter: brightness(0.7);
  filter: brightness(0.7);
}
.person {
  margin-top: 48px;
}
.person .icon-circle-right {
  color: #7cedf5;
}
.person .aboutmesometings {
  margin-bottom: 46px;
}
.slider img {
  width: 100%;
  text-align: center;
  top: 12%;
  content: "";
  display: block;
  height: 100px;
  width: 100px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  margin-left: -50px;
  z-index: 10;
}
.msg {
  position: absolute;
  width: 100px;
  height: 40px;
  text-align: center;
  top: 31%;
  left: 50%;
  margin-left: -50px;
  z-index: 20;
}
h3 {
  color: #fff;
  font-size: 10px;
  margin: 0;
  height: 20px;
}
#xi {
  font-size: 10px;
  color: #fff;
}
.my-tabs {
  margin-top: -5px;
  background-color: #fff;
}
.mu-tab-text {
  color: #2C91DE;
}
.my-tabs {
  margin-top: -5px;
  background-color: #fff;
}
.my-tabs > .mu-tab-link-highlight {
  background-color: #2C91DE;
}
.aboutmesometings {
  width: 100%;
}
.my-msg {
  text-align: center;
  margin-top: 10px;
  font-size: 12px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 15px;
  padding-bottom: 2px;
  padding-top: 2px;
}
.my-msg a {
  color: #fff;
}
.personcomm {
  padding-top: 5px;
  width: 100%;
  height: 220px;
  border: 1px solid #fff;
  position: relative;
  color: #000;
}
.headpic {
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  border-radius: 25px;
  position: absolute;
  left: 3px;
}
.name {
  height: 20px;
  font-size: 14px;
  border: 1px solid #fff;
  position: absolute;
  left: 60px;
  top: 3px;
  padding: 2px;
}
.time {
  position: absolute;
  left: 60px;
  border: 1px solid #fff;
  font-size: 10px;
  top: 22px;
  padding: 4px 2px 2px 4px ;
  color: #999999;
}
.flower {
  font-size: 14px;
  position: absolute;
  right: 5px;
  top: 5px;
  padding: 2px;
  color: #12dff5;
}
.text {
  border: 1px solid #fff;
  position: absolute;
  left: 60px;
  right: 5px;
  top: 44px;
  padding: 2px;
}
.pic {
  position: absolute;
  border: 1px solid #fff;
  left: 60px;
  right: 5px;
  padding: 2px;
  top: 90px;
}
.flex-demo {
  height: 80px;
  border: 1px solid #fff;
}
.flex-demo img {
  height: 80px;
}
.oper {
  position: absolute;
  border: 1px solid #fff;
  left: 60px;
  right: 5px;
  padding: 2px;
  top: 180px;
}
.zan {
  padding-left: 20px;
}
.zan img {
  position: absolute;
  left: 0px;
  top: -1px;
}
.oper-demo span {
  color: #999999;
  font-size: 14px;
}
.pic {
  height: 80px;
}



