
#booklist {
  margin-top: 46px;
  margin-bottom: 66px;
}
#booklist img {
  width: 100px;
  height: 120px;
  position: absolute;
}
.bookshelf {
  width: 100%;
  height: 100%;
  padding-top: 20px;
  margin-left: 15px;
  position: relative;
  margin-top: 10px;
}
.bookshelf .book_data {
  border: 1px solid #fff;
  margin-left: 120px;
  padding-right: 20px;
}
.bookshelf .bookname {
  margin-top: 10px;
  font-size: 18px;
  margin-bottom: 5px;
}
.bookshelf .bookdes {
  margin-top: 0px;
  margin-bottom: 0px;
  color: #454545;
  height: 40px;
  border: 1px solid #fff;
}
.bookshelf .bookauthor {
  margin-top: 10px;
  margin-bottom: 5px;
  color: #787878;
}



