
@import '../common/stylus/color.css';
@import '../common/stylus/style.css';
.aboutbook img {
  width: 100px;
  height: 120px;
  position: absolute;
  margin-left: 20px;
  margin-top: 20px;
}
.aboutright {
  border: 1px solid #fff;
  margin-left: 140px;
  margin-top: 5px;
}
.book_name {
  margin-bottom: 0px;
  font-size: 18px;
}
.book_author {
  margin-top: 5px;
  margin-bottom: 5px;
  color: #898989;
}
.book_satr {
  color: #12dff5;
}
.book_satr .book_mark {
  color: #989898;
  margin-left: 5px;
}
.book_introduce {
  margin-top: 40px;
  position: absolute;
  width: 100%;
}
.book_introduce h2 {
  margin-left: 10px;
}
.book_introduce h4 {
  margin-left: 10px;
}
.book_introduce .mu-list {
  height: 200px;
}
.book_introduce div .mu-tab-text {
  color: #989898;
}
.book_introduce .mu-tab-link-highlight {
  background-color: #7cedf5;
}
.book_introduce .booktab1 {
  height: 200px;
}
.book_introduce .booktext {
  font-family: sans-serif;
  text-indent: 20px;
  letter-spacing: 4px;
  margin-left: 10px;
  margin-right: 10px;
  line-height: 22px;
  text-align: justify;
  text-justify: inter-ideograph;
  height: 150px;
  overflow-y: auto;
}
.book_introduce .bookevaluation {
  width: 100%;
  height: 90px;
  margin-bottom: 10px;
  position: relative;
}
.book_introduce .bookevaluation .headpic {
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  border-radius: 25px;
  position: absolute;
  left: 3px;
}
.book_introduce .bookevaluation .name {
  height: 20px;
  font-size: 14px;
  border: 1px solid #fff;
  position: absolute;
  left: 60px;
  top: 3px;
  padding: 2px;
}
.book_introduce .bookevaluation .time {
  position: absolute;
  left: 60px;
  border: 1px solid #fff;
  font-size: 10px;
  top: 22px;
  padding: 4px 2px 2px 4px ;
  color: #999999;
}
.book_introduce .bookevaluation .text {
  border: 1px solid #fff;
  position: absolute;
  left: 60px;
  right: 5px;
  top: 44px;
  padding: 2px;
}
.guodu {
  position: absolute;
}
#bookdata {
  margin-top: 56px;
}
#bookdata .mu-appbar {
  height: 48px;
  position: fixed;
  top: 0px;
  z-index: 999;
}
#bookdata .bookbottom {
  position: fixed;
  bottom: 0px;
  z-index: 888;
}
#bookdata .addshelf {
  color: #12dff5;
  padding-top: 20px;
}
#bookdata .nowreading {
  background-color: #12dff5;
  color: #fff;
  padding-top: 20px;
}
#bookdata .download {
  color: #12dff5;
  padding-top: 20px;
}



