
@import '../common/stylus/style.css';
.topiccomm img {
  width: 100%;
  height: 200px;
}
.topiccomm p {
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.topiccomm h4 {
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 5px;
  margin-bottom: 10px;
}
.topiccomm .personcomm {
  padding-top: 5px;
  width: 100%;
  height: 220px;
  border: 1px solid #fff;
  position: relative;
  color: #000;
}
.topiccomm .headpic {
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  border-radius: 25px;
  position: absolute;
  left: 3px;
}
.topiccomm .name {
  height: 20px;
  font-size: 14px;
  border: 1px solid #fff;
  position: absolute;
  left: 60px;
  top: 3px;
  padding: 2px;
}
.topiccomm .time {
  position: absolute;
  left: 60px;
  border: 1px solid #fff;
  font-size: 10px;
  top: 22px;
  padding: 4px 2px 2px 4px ;
  color: #999999;
}
.topiccomm .flower {
  font-size: 14px;
  position: absolute;
  right: 5px;
  top: 5px;
  padding: 2px;
  color: #12dff5;
}
.topiccomm .egtopic {
  border: 1px solid #fff;
  position: absolute;
  left: 63px;
  right: 5px;
  top: 40px;
  padding: 2px;
}
.topiccomm .text {
  border: 1px solid #fff;
  position: absolute;
  left: 60px;
  right: 5px;
  top: 60px;
  padding: 2px;
}
.topiccomm .pic {
  position: absolute;
  border: 1px solid #fff;
  left: 60px;
  right: 5px;
  padding: 2px;
  top: 105px;
}
.topiccomm .flex-demo {
  height: 80px;
  border: 1px solid #fff;
}
.topiccomm .flex-demo img {
  height: 80px;
}
.topiccomm .oper {
  position: absolute;
  border: 1px solid #fff;
  left: 60px;
  right: 5px;
  padding: 2px;
  top: 190px;
}
.topiccomm .zan {
  padding-left: 20px;
}
.topiccomm .zan img {
  position: absolute;
  left: 0px;
  top: -1px;
  width: 22px;
  height: 22px;
}
.topiccomm .oper-demo span {
  color: #999999;
  font-size: 14px;
}
.topiccomm .setting {
  position: absolute;
  right: 3px;
  bottom: 5px;
}
.topiccomm .pic {
  height: 80px;
}



