
.select {
  background: #fff;
}
.select .banner {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
}
.select .banner .box {
  height: 200px;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.select .banner .img {
  height: 200px;
  width: 100%;
  transition: all 1s;
}
.select .banner .num {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  position: absolute;
  bottom: 0;
  right: 10px;
}
.select .banner .num .active {
  background: #058 !important;
}
.select .banner .num li {
  height: 10px;
  width: 10px;
  cursor: pointer;
  background: #fff;
  border-radius: 50%;
  margin: 10px;
}


