
@import '../common/stylus/color.css';
.title_data {
  font-size: 18px;
}
.all_data {
  margin-left: 5px;
  margin-right: 5px;
}
.all_data div .mu-text-field {
  width: 80px;
  position: absolute;
  top: -25px;
  right: -1px;
}
.detial_data {
  font-size: 18px;
  border: 1px solid #fff;
  padding-top: 10px;
  padding-bottom: 5px;
  margin-top: 5px;
  position: relative;
}
.hr {
  margin-top: 10px;
  margin-bottom: 10px;
}
.headimg_data {
  border: 1px solid #fff;
  width: 42px;
  height: 42px;
  border-radius: 26px;
}
.headimg_data img {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  position: absolute;
  right: 5px;
  bottom: 1px;
}
.backimg_data {
  border: 1px solid #fff;
  width: 40px;
  height: 40px;
}
.backimg_data img {
  width: 40px;
  height: 40px;
  position: absolute;
  right: 5px;
  bottom: 1px;
}
.birth_data .mu-text-field-hint {
  width: 90px;
}
.birth_data div .mu-text-field-line {
  color: #fff;
  background-color: #fff;
}
.birth_data div .mu-text-field-hint {
  color: #989898;
}
.birth_data div .mu-text-field-input {
  color: #989898;
}



