
.push span {
  font-size: 17px;
  line-height: 1.5em;
  font-family: "Arial", "Microsoft YaHei", "", "", sans-serif;
}
.mu-text {
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  color: #000;
}
.mu-text .mu-text-field-hint {
  color: #989898;
}
.mu-text textarea {
  color: #000;
}
.carma {
  width: 80px;
  height: 80px;
  border: 1px solid #F7F7F7;
  background-color: #F7F7F7;
  margin-left: 10px;
}
.carma .icon-image {
  font-size: 30px;
  color: #D4D4D4;
  text-align: center;
  margin-top: 20px;
}
.talk_text {
  font-size: 12px;
  text-align: center;
  color: #D4D4D4;
}
hr {
  margin-left: 5px;
  margin-right: 5px;
}
.talk_address {
  font-size: 18px;
  margin-left: 5px;
  margin-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.talk_right_add {
  position: absolute;
  right: 5px;
  color: #CFCFCF;
  padding-top: 1px;
}
.talk_mm {
  font-size: 18px;
  margin-left: 5px;
  margin-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.talk_all {
  position: absolute;
  color: #B5B5B5;
  right: 30px;
  font-size: 16px;
  padding-top: 2px;
}
.talk_right_mm {
  position: absolute;
  right: 5px;
  color: #CFCFCF;
  padding-top: 1px;
}
.demo-popup-bottom {
  width: 100%;
  text-align: center;
}



