
@import '../common/stylus/color.css';
.searchPage {
  position: relative;
}
.for_data {
  position: absolute;
  background-color: #fff;
  border: 1px solid #fff;
  z-index: 999;
}
.eg_book {
  margin-left: 5px;
  color: #000000;
  position: absolute;
  top: 56px;
}
.eg_book span {
  border: 1px solid #989898;
  padding: 3px 6px 3px 6px;
  margin-right: 20px;
}
.eg_book ul li {
  list-style: none;
  margin-bottom: 20px;
  padding-left: 5px;
}



